/* Variables */
/* LESS CSS */
body {
  font-family: 'Open Sans', arial, sans-serif;
  background: #ffffff;
  color: #591e23;
}
.bottomPadding {
  padding-bottom: 50px;
}
/***** Navbar ********/
.navbar {
  background-color: #ffffff;
  text-align: center;
  min-height: auto;
  font-size: 13px;
  width: auto;
  border-bottom: 1px solid #d94e67;
  padding: 0;
}
.navbar-brand {
  height: 58px;
  font-size: 30px;
}
#brand {
  font-size: 30px;
}
nav a {
  color: #591e23;
  font-style: bold;
}
.nav li a:focus,
.nav li a:hover {
  background-color: rgba(166, 133, 114, 0.6);
}
.icon-bar {
  background: #591e23;
}
nav a:hover {
  color: #d94e67;
}
nav ul a {
  font-size: 20px;
}
/****** Middle Sections ********/
.section {
  padding: 10px 30px;
}
#section0 {
  padding: 20px 0;
}
/****** Footer ********/
#footer {
  color: rgba(0, 0, 0, 0.1);
}
/***** Media Devices ********/
@media (min-width: 768px) {
  .navbar-nav {
    position: relative;
    right: -50%;
  }
  .navbar-nav > li {
    position: relative;
    left: -50%;
  }
  .navbar-nav > li a {
    line-height: 60px;
    vertical-align: middle;
    padding: 0 24px;
  }
}
@media (min-width: 992px) {
  .navbar-nav > li a {
    padding: 0 20px;
  }
}
@media (min-width: 1200px) {
  .navbar-nav > li a {
    padding: 0 20px;
  }
}

